import axios,{AxiosError,AxiosInstance,AxiosResponse} from "axios";

import configKeys from "../../../utils/config";


const adminSetupAxiosInterceptors = (): AxiosInstance => {
    const api: AxiosInstance = axios.create({
      baseURL: configKeys.API_URL,
    });
    api.interceptors.request.use(
      (config: any) => {
        const token = localStorage.getItem("adminToken");
        if (token) {
          config.headers.authorization = `Bearer ${token}`;
        }
  
        return config;
      },
      (error: AxiosError) => {
        return Promise.reject(error);
      }
    );
  
    api.interceptors.response.use(
      (response: AxiosResponse) => response,
      (error) => {
        if (error.response?.status === 401) {
          //uauthorized error, clear token and redirect to login page
          //call clear token function from store here
          window.location.replace("/admin/login");
        }
        return Promise.reject(error);
      }
    );
    return api
}

export default adminSetupAxiosInterceptors;